table tr td{
    border: 1px solid;
}

.table {
    font-family: Arial, Helvetica, sans-serif;
    border-collapse: collapse;
    width: 100%;
    z-index: 1;
  }
  
  .table td, .table th {
    border: 1px solid #ddd;
    padding: 8px;
  }
  
  .table tr:nth-child(even){background-color: #f2f2f2;}
  
  .table tr:hover {background-color: #ddd;}
  
  .table th {
    padding-top: 12px;
    padding-bottom: 12px;
    text-align: left;
    background-color: #04AA6D;
    color: white;
  }

.filtering {
    padding: 15px;
    margin-top: 10px;
    border: 1px solid black;
    border-radius: 5px;
    position: absolute;
    z-index: 2;
    background-color: white;
    box-shadow: 0px 1px 2px 2px rgb(94, 93, 93);
}

.categoriesContainer {
  display: flex;
}

.categories {
  display: flex;
  flex-direction: column;
  flex: 1;
  border-radius: 10px;
  padding: 5px
}

.categories:last-child {
 background-color: rgb(227, 227, 227);
}

.section {
 display: flex;
 padding: 20px 20px;
 padding-top: 10px;
}

.infoBlock {
  /* padding: 40px 20px; */
  display: flex;
  gap: 40px
}

.searchInput {
  height: 30px;
  border: #2c0cbc solid;
}

.header {
  display: flex;
  justify-content: space-between;
  top: 0px;
  padding: 20px;
  padding-bottom: 10px;
  position: sticky;
  background-color: white;
  z-index: 2;
}

.categoriesButton {
  height: 38px;
  cursor: pointer;
}

.salesButton {
  margin-left: 15px;
  height: 30px;
  cursor: pointer;
}

.categoryLabel:hover {
  cursor: pointer;
  background-color: #f8f7a4;
}

.saleSuccess {
  background-color: #04AA6D;
  color: white;
}

.rublePrice {
  color: gray;
  font-size: 14.5px;
}

.marquee {
  position: sticky;
  left: 0;
  bottom: 0;
  z-index: 4;
  background-color: white;
  padding: 3px 0;
}

.orderButton {
  background: linear-gradient(135deg, rgb(171, 224, 171), rgb(98, 234, 188));
  color: balck;
  border-radius: 10px;
  padding: 10px 15px;
  font-size: 16px;
  cursor: pointer;
}